<template>
  <div id="app">
      <navbar />
      <keep-alive>
        <router-view v-if='!$route.meta.productContent && !$route.meta.shoppingCart && !$route.meta.userData && !$route.meta.login' />
      </keep-alive>
        <router-view v-if='$route.meta.productContent || $route.meta.shoppingCart || $route.meta.userData || $route.meta.login' />

      <footer>
