            transform: translateY(-50%);
            z-index: -1;
        }

        main .color h2 span,
        main .specification h2 span {
            z-index: 50;
            width: 180px;
            display: inline-block;
            background: #000;
            font-size: 1.6rem;
        }

        main .color .colorlist {
            margin-top: 20px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            /* background-color: chartreuse; */
            margin-bottom: 20px;
        }

        main .color .colorlist h3 {
            width: 100%;
            height: 30px;
            line-height: 30px;
            text-align: center;
            font-size: 1.4rem;
        }

        main .color .colorlist h3 span {
            background-color: rgb(236, 174, 97);
            padding: 10px 15px 10px 15px;
            border: 2px solid whitesmoke;
            border-radius: 15px;
            color: #000;
        }

        main .color .colorlist .coloritem {
            width: 300px;
            height: 300px;
            /* background-color: brown; */
            text-align: center;
            margin-top: 20px;
        }

        main .color .colorlist .coloritem img {
            width: 270px;
            height: 270px;
        }

        main .specification {
            width: 80%;
        }

        main .specification .specificationSelect {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 20px;
        }

        main .specification .specificationSelect select {
            margin-bottom: 20px;
