            height: 30px;
            width: 200px;
            background-color: blanchedalmond;
            font-size: large;
            font-weight: bolder;
            font-family: '微軟正黑體';
            color: #000;
        }
        main .specification .specificationSelect select option{
            color: #000;
        }
        main .specification .specificationSelect table {
            width: 400px;
            text-align: center;
