        }
        footer {
            text-align: center;
        }

        @media screen and (max-width:576px) {
            header {
                padding-top: 60px;
            }
        }

        @media screen and (max-width:400px) {
            main .specification .specificationSelect table {
                width: 350px;
            }
        }
</style>