<template>
    <div id="content">
        <div id="login" >
                <div id="loginicon">會員登入</div>
                <div id="logincontent" v-if='!status && !findPassword'>
                    <div>
                        <label for="username" class='text'>帳號</label>
                        <input type="text" class="inputtext" id='username' name='username'>
                    </div>
                    <div class='passwordarea'>
                        <label for="password" class='text'>密碼</label>
                        <input type="password" class="inputtext" id='password' name='password'>
                        <button @click='findPassword= true'>忘記密碼</button>
                    </div>
                    <div class='formbuttonarea'>
                        <input type="submit" value="登入" @click='submitForm($event)'></input>
                        <router-link to='/registered'> 
                            <button type="button">註冊</button>
                        </router-link>
                    </div>
                </div>
                <div id='msg' v-if='status'>
                    <p v-text="msg"></p>
                </div>
                <ForgotPassword  v-if='findPassword' @status-Update='statusChange' />
        </div>

        
    </div>
</template>

<script>
import ForgotPassword from "@/components/ForgotPassword.vue";
export default ({
    data(){
        return{status:false,msg:null,findPassword:false}
    },
    components:{
        ForgotPassword,
    },
    methods:{
        submitForm(e){
            const Rules = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d]{1,}$/;
            if(!Rules.test(document.querySelector('#username').value)){
                alert('帳號格式有誤，請再次確認');
                document.querySelector('#username').focus();
                return;
            }
            if(!Rules.test(document.querySelector('#password').value)){
                alert('密碼格式有誤，請再次確認');
                document.querySelector('#password').focus();
                return;
            }
            axios.post('/api/data.php', JSON.stringify({username:document.querySelector('#username').value,password:document.querySelector('#password').value,dataKey:12}))
                .then(res => {
                    if(res.data.username  === document.querySelector('#username').value){
                        Cookies.set('username',res.data.username);
                        Cookies.set('randid',res.data.randid);
                        this.msg = res.data.name + '您好，現在將為您跳轉至會員頁面。';
                        setTimeout( ()=>{
                            this.$router.push({name:'userData'});
                        } ,2000)
                        this.status = true;
                    }
                    else{
                        this.msg = res.data;
                        this.status = true;
                        setTimeout( ()=>{
                            this.status = false;
                        } ,1500)
                    }
                })
        },
        statusChange(){
            this.findPassword = false;
        }
    }
})
</script>

<style scoped>
        #content {
            height:100vh;
            width: 100%;
            padding-top:120px;
            background-color: #000;
        }

        #login {
            position: relative;
            top: 30px;
            left: 18.5%;
            height: 39.5%;
            width: 60.5%;
            background-color: #000;
        }

        #loginicon {
            height: 50px;
            background-color: #000;
            border-bottom: 0.26vw solid red;
            color: #fff;
            font-size: 1.9rem;;
            text-align: left;
        }

        #logincontent {
            position: relative;
            top: 5vW;
            height: 320px;
        }

        .passwordarea {
            position: relative;
        }

        .passwordarea button {
            position: absolute;
            width: 70px;
            top: 65px;
            right: 2.4vw;
            color: white;
            background-color: transparent;
            font-weight: bold;
            border: none;
            cursor: pointer;
            text-decoration-line: underline;
        }  

        .passwordarea button:hover {
            color: rgb(228, 208, 127);
        }
        #msg{
            font-size: 1.6rem;
            color: white;
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 180px;         
        }
        .text {
            float: left;
            text-align: center;
            width: 21.7%;
            height: 50px;
            color: white;
            font-size: 28px;
            margin-top: -2px;
        }

        .inputtext {
            outline: none;
            height: 35px;
            width: 68.7%;
            border-radius: 17px;
            margin-bottom: 50px;
            font-size: 25px;
            background: #000;
            color: #fff;
        }

        .formbuttonarea {
            text-align: center;
        }

        .formbuttonarea a button,
        .formbuttonarea input {
            color: white;
            padding: 0 50px;
            font-size: 1.2rem;
            font-weight: bold;
            border-radius: 29px;
            text-decoration: none;
            border: 2px solid rgb(235, 235, 235);
            background: #000;
            cursor: pointer;
            margin: 0 10px;
        }
        .formbuttonarea a button:hover,
        .formbuttonarea input:hover {
            background: rgb(235, 235, 235);
            color: black;
        }
        .formbuttonarea button:nth-child(2) {
            margin-left: 50px;
        }

        @media screen and (max-width:768px) {
            .text {
                line-height: 50px;
                font-size: 20px;
                margin-top: -6px;
            }
        }

        @media screen and (max-width:576px) {
            #span {
                height: 60px;
                background-color: #000;
            }

            .formbuttonarea button,
            .formbuttonarea input {
                margin: 10px 0;
            }

            .formbuttonarea button:nth-child(2) {
                margin-left: 0;
            }
        }
</style>
