<template>
    <div id="content">
        <div id="contenticon">
            <h1>會員中心</h1>
        </div>
        <div id="userdata">
            <div id="pos" v-if='userdata && !componentName'>
                <div class="databox">
                    <div class="text">帳號
                    </div>
                    <div class="">{{userdata[0].username}}</div>
                </div>
                <div class="databox">
                    <div class="text">姓名</div>
                    <div class="">{{userdata[0].name}}</div>
                </div>
                <div class="databox">
                    <div class="text">身分證字號</div>
                    <div class="">{{userdata[0].idNumber}}</div>
                </div>

                <div class="databox">
                    <div class="text">手機號碼</div>
                    <div class="">{{userdata[0].phone}}</div>
                </div>
                <div class="databox">
                    <div class="text">電子信箱</div>
                    <div class="">{{userdata[0].email}}</div>
                </div>
                <div class="databox">
                    <div class="text">註冊時間</div>
                    <div class="">{{userdata[0].date}}</div>
                </div>
                <div class="databox">
                    <div class="text">地址</div>
                    <div class="">{{userdata[0].address}}</div>
                </div>
                <div class="databox">
                    <div class="apos">
                        <a href="#" @click='componentChange($event,"Modify")'>個人資料修改</a>
                        <a href="#" @click='componentChange($event,"componentName")'>密碼修改</a>
                        <a href="#" @click='componentChange($event,"order")'>訂單查詢</a>
                        <a href="#" @click='logout($event)'>登出</a>
                    </div>
                </div>
            </div>
            <div v-if='!status' key='block-b' class='loading'>
                <Logining></Logining>
            </div>
            <div v-if="status && !userdata && !logoutmsg" key='block-e' class="error">
                <p>您尚未登入，請先登入。</p> 
                <router-link to='/login'>前往登入頁面</router-link>
            </div>
            <div v-if="logoutmsg" key='block-f' class="logoutmsg">
                <p>您已順利登出</p> 
                <router-link to='/'>回首頁</router-link>
            </div>
            <Modify :userdata='userdata' @status-Update='statusChange' v-if='componentName == "Modify"' key='block-g'/>
            <ChangePassword v-if='componentName == "componentName"' @status-Update='statusChange'  />
            <Order v-if='componentName == "order"' @status-Update='statusChange' :userdata='userdata'/>
        </div>
    </div>
</template>

<script>
import Modify from '@/components/Modify.vue';
import ChangePassword from '@/components/ChangePassword.vue';
import Order from '@/components/Order.vue';
import Logining from '@/components/Logining.vue'


export default ({
        data(){
                return {
                    componentName:null,
                    status:false,
                    cookiesData:null,
                    userdata:null,
                    logoutmsg:false,
                };
            },
        components:{
            Modify,
            ChangePassword,
            Order,
            Logining
        },
        created(){
            this.cookiesData = Cookies.get('username');
                if(this.cookiesData){
                    axios.post('/api/data.php', JSON.stringify({name:this.cookiesData,dataKey:4}))
                    .then(res=>{
                        this.userdata = res.data;
                        this.status = true;
                        Object.freeze(this.cookiesData);
                    })
                    .catch((error) => { console.error(error) });
                }
                else{
                    this.$nextTick(function(){
                        this.status = true;
                    });
                }
        },
        methods:{
            logout(e){
                e.preventDefault();
                Cookies.set('username','');
                Cookies.set('randid','');
                this.userdata = null;
                this.logoutmsg = true;
            },
            componentChange(e,pagename){
                e.preventDefault();
                this.componentName = pagename;
            },
            statusChange(){
                this.componentName= null;
            }
        },
        beforeRouteEnter(to,from,next){
            next(vm=>{
                vm.componentName = null;
                vm.logoutmsg = false;
            });
        }
})
</script>

<style scoped>
        #content {
            padding-top:130px;
            width: 60.5VW;
            margin: 0 auto 0;
        }

        #contenticon h1 {
            padding-bottom:10px;
            border-bottom: 0.26VW solid red;
            color: white;
            text-align: left;
            font-size:1.9rem;
            margin-bottom: 25px;
        }

        @media screen and (max-width:576px) {
            #span {
                height: 60px;
                background-color: #000;
            }

        }
        #userdata {
            background-color: #000;
            border: 3px solid rgb(235, 235, 235);
            border-radius: 10px;
            min-height: 180px;
            position: relative;
        }

        #content .loading,#content .error,#content .logoutmsg{
            position:absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
        }
        #content .error,#content .logoutmsg{
            text-align: center; 
            font-size: 1.6rem; 
            margin-bottom: -2vh; 
            color:white;
        }
        
        #pos {
            margin: 3vh 0 4vh 5vw;
        }

        .databox {
            display: flex;
            line-height: 30px;
            font-size: 18px;
            color: white;
            font-weight: bold;
        }
        .databox .apos{
            width: 100%;
            display: flex;
            justify-content:space-evenly;
            flex-wrap: wrap;
        }
        .databox .apos a{
            margin-top: 5px;
            margin-left: 5px;
        }
        .text {
            margin-right: 2vw;
            width: 90px;
            height: 30px;
            text-align: center;
        }

        #pos .databox+.databox {
            margin-top: 5vh;
        }
        .databox a,#content .error p,#content .logoutmsg p{
            margin-bottom: 25px;
        }

        .databox a ,#content .error a,#content .logoutmsg a{
            text-decoration: none;
            color: white;
            border: 2px solid rgb(235, 235, 235);
            border-radius: 30px;
            padding: 5px 15px;
            background: #000;
            margin-top:1.5vh;
            cursor: pointer;
        }
        #content .error a,#content .logoutmsg a{
            margin-right: 0;
            margin-bottom: 1.5vh;
        }

        .databox a:hover,#content .error a:hover, .logoutmsg p + a:hover {
            background: rgb(235, 235, 235) !important;
            color: black !important;
        }
        .sk-chase {
            width: 40px;
            height: 40px;
            position: relative;
            animation: sk-chase 2.5s infinite linear both;
        }

        @media screen and (max-width:756px) {
            .databox a {
                display: block;
                text-align: center;
                margin: 2vh 0 0 0 ;
            }

            .databox {
                flex-direction: column;
            }
            #pos{
                margin: 3vw;
            }

            .text {
                border-bottom: 1px solid red;
                margin:0 0 1vh 0;
                width: 100%;
            }
            #pos .databox+.databox {
                margin-top: 2vh;
            }
        }
        @media screen and (max-width:631px) {
            #content #userdata .error {
                font-size: 1.4rem;
            }
        }
        @media screen and (max-width:576px) {
            #content #userdata .error {
                font-size: 1.1rem;
            }
            #content .error a {
                padding: 5px 10px;
            }
        }
        @media screen and (max-width:450px) {
            #content #userdata .error {
                font-size: 0.9rem;
            }
            #content .error a {
                padding: 5px 5px;
            }
        }
</style>