<template>
    <header id='banner'>
        <button @click='imgChange(now+1)'><i class="fas fa-chevron-right"></i></button>
        <button @click='imgChange(now-1)'><i class="fas fa-chevron-left"></i></button>
        <div class="imgContainer">
            <transition-group name='bannerChange'>
                <div class="imgList" v-for='item of showimg' :key='item.id'>
                    <router-link :to="item.href">
                        <img :src="item.src" alt="">
                    </router-link>
                </div>
            </transition-group>
        </div>
    </header>
</template>

<script>

export default ({
        name:'indexBanner',
        data() {
            return {
                now: 0,
                imgs: [{ id: 1, src: 'image/banner1.jpg', href: '/productContent/34' },
                { id: 2, src: 'image/banner2.jpg', href: '/productContent/7' },
                { id: 3, src: 'image/banner3.jpg', href: '/productContent/35' }],
                time: null,
            }
        },
        mounted() {
            this.time = setTimeout(() => {
                this.now++;
                this.imgChange(this.now);
            }, 4000);
        },
        computed: {
            Allimages() {
                let ary = [];
                let total = this.imgs.length;
                while (ary.length < 5) {
                    let key = ary.length / total;
                    for (let i = 0; i < total; i++) {
                        ary.push({ id: key + '-' + this.imgs[i].id, src: this.imgs[i].src, href: this.imgs[i].href });
                    }
                }
                return ary;
            },
            showimg() {
                return this.Allimages.slice(this.now).concat(this.Allimages.slice(0, this.now));
            }
        },
        methods: {
            imgChange(index) {
                clearTimeout(this.time);
                this.now = index > this.Allimages.length - 1 ? 0 : index < 0 ? this.Allimages.length - 1 : index;
                this.time = setTimeout(() => {
                    this.now++;
                    this.imgChange(this.now);
                }, 4000);
            }
        },
})
</script>

<style scoped>
        #banner {
            background: #000;
            display: flex;
            max-height: 1030px;
            width: 100%;
            position: relative;
            overflow: hidden;
            /* padding-top:120px; */
        }

        #banner button {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            background-color: transparent;
            border: none;
            color: white;
            font-size: 72px;
            cursor: pointer;
            transition: background 0.4s;
        }

        #banner button:hover {
            background-color: rgba(192, 192, 192, 0.41);
        }

        #banner button:nth-child(1) {
            right: 0;
        }

        #banner button:nth-child(2) {
            left: 0;
        }

        #banner .imgContainer span {
            display: flex;
            flex-wrap: nowrap;
            margin-left: -100vw;
        }

        #banner .imgContainer .imgList {
            width: 100vw;
        }

        #banner .imgContainer .imgList:last-child {
            Visibility: hidden;
        }

        #banner .imgContainer .imgList img {
            max-width: 100%;
            height: auto;
        }

        .bannerChange-move {
            transition: all 0.7s;
        }
        @media screen and (max-width:576px){
            #banner {
                padding-top: 60px;
            }
            #banner button {
                font-size: 36px;
            }

        }
</style>
