<template>
    <div class="container" id='news'>
        <section class='rowtitle'>
           <span>最新消息</span> 
        </section>
        <section class='newslist'>
                <transition-group name='newItem'>
                    <div class="newsitem"  v-for='item of resdata' :key='item.id'> 
                        <div class="newsimg">
                            <img :src="item.Thumbnail" alt="" v-if='item.Thumbnail' style="width:100%; height:100%;">
                            <img src="image/Logo.jpg" alt="" v-else  style="width:100%; height:100%;">
                        </div>
                        <div class="newscontent">
                            <h2>{{item.title}}</h2>
                            <div class="newstext" v-text='contentFilter(item.content)'>
                            </div>
                            <div class="buttonarea">
                                <router-link :to="`/newsContent/${item.id}`" >
                                    <button>MORE</button>
                                </router-link>
                            </div>
                        </div>
                    </div>
                </transition-group>
                <div class="load" v-if='loading'>
                    <Logining />
                </div>
                <div id="listEnd" ></div>
        </section>
    </div>
</template>

<script>
import Logining from '@/components/Logining.vue'

export default ({
    
    name:'news',
            data(){
                return {
                    resdata:null,
                    observer:null,
                    dataNumber:4,
                    dataindex:0,
                    newsRows:0,
                    loading:false,
                };
            },
            components:{
                Logining,
            },
            methods:{
                contentFilter(content){
                    if(content.length<=70){
                        return content;
                    }
                    else{
                        if(window.innerWidth>1500){
                            return content.slice(0,300)+ '....';
                        }
                        else if(window.innerWidth>1050){
                            return content.slice(0,180)+ '....';
                        }
                        else if(window.innerWidth>768){
                            return content.slice(0,120)+ '....';
                        }
                        else if(window.innerWidth>576){
                            return content.slice(0,70)+ '....';
                        }
                        else{
                            return content.slice(0,250)+ '....';
                        }
                    }
                },
                loadnewData(entries){
                    if(this.resdata!=null  && this.$route.path !== '/'){
                        entries.forEach(entry => {
                            if (entry.isIntersecting) {
                                if(!this.loading){
                                    this.loading = true;
                                    axios.post('/api/data.php', JSON.stringify({dataNumber:this.dataNumber,dataindex:this.dataindex, needNewsRows:false, dataKey:8 }))
                                    .then(res=>{
                                        this.resdata.push(res.data[0]);
                                        this.dataindex ++;
                                        this.loading = false;
                                        if(this.dataindex >=this.newsRows){
                                            this.observer.unobserve(entry.target);
                                            document.querySelector('#listEnd').style.display='none';
                                        }
                                    })
                                }
                            }
                        })
                    }
                }
            },
            mounted(){
                axios.post('/api/data.php', JSON.stringify({dataNumber:this.dataNumber,dataindex:this.dataindex, needNewsRows:true, dataKey:8}))
                    .then(res=>{
                        
                        this.resdata = res.data;
                        this.newsRows = this.resdata[this.resdata.length -1];
                        this.resdata.pop();
                        this.dataindex= this.dataNumber;
                        this.dataNumber =1;
                        this.observer = new IntersectionObserver(this.loadnewData,{
                            threshold:1,
                        });    
                        this.observer.observe(document.getElementById('listEnd'));
                    })

            },

})
</script>

<style scoped>
        .container{
            display: grid;
            grid-template-columns: 10vw 80vw 10vw;
            grid-template-rows: 120px 50px 1fr;
            grid-row-gap: 30px;
        }
        .rowtitle{
            position: relative;
            grid-row:2/3;
            grid-column:2/3;
            line-height: 50px;
            color: white;
            text-align: center;
            font-size: 2rem;
            z-index: -2;
        }
        .rowtitle span{
            background: #000;
            display: inline-block;
            width: 180px;
            z-index: 5;
        }
       .rowtitle::before{
            content: '';
            position: absolute;
            border-top: 4px solid red;
            top: 25px;
            left:0px;
            right: 0px;
            z-index: -1;
        }

        .newslist{
            grid-row:3/4;
            grid-column: 2/3;
        }
        .newsitem{
            display: flex;
            width:100%;
            height: 200px;
            background-color: rgb(87, 87, 87);
            margin-bottom: 30px;
            color: white;
        }
        .newsitem:nth-child(even){
            flex-direction: row-reverse;
        }
        .newsimg{
            width:20%;
            min-width: 200px;
            height: 100%;
            /* background: rgb(185, 207, 104); */
        }
        .newscontent{
            display: flex;
            flex-direction: column;
            width:80%;
            height: 100%;
        }
        .newscontent .newstext{
            height: 90%;
            overflow: hidden;
            text-overflow:ellipsis;
            min-width:0;
        }
        .newscontent .buttonarea{
            height: 10%;
            width: 100%;
            text-align: center;
            margin-top:13px;
            margin-bottom: 15px;
            padding-bottom: 5px;
        }
        .newscontent .buttonarea a button{
           padding: 5px 25px;
           color: white;
           box-sizing: border-box;
           cursor: pointer;
           transition: all 0.3s;
            border-radius: 29px;
            background: #000;
            border: 2px solid rgb(235, 235, 235);
        }
        .newscontent .buttonarea a button:hover{
            background: rgb(235, 235, 235);
            color: black;
        
        }
        .newscontent h2{
            text-align: left;
            margin-top: 5px;
            margin-left: 5px;
            margin-bottom: 5px;
            font-size: 1.7rem;
            font-weight: bold;
        }
        .newscontent .newstext{
            margin-left: 5px;
            margin-top: 5px;
            line-height: 22px;
            padding-right: 5px;
            text-align: justify;
        }
        .newItem-enter{
            opacity: 0;
        }
        .newItem-enter-active{
            position: relative;
            left:-100%;
            transition: all 0.6s;
        }
        .newItem-enter-to{
            opacity: 1;
            left:0px;
        }
        #listEnd{
            height: 50px;
        }
        @media screen and (max-width:768px){
            .container{
                grid-template-columns: 1fr 6fr 1fr;
            }
        }
        @media screen and (max-width:576px){
            .container{
                grid-template-rows: 60px 50px 1fr 50px;
            }
            .newsitem{
                height: 550px;
                flex-direction: column;
            }
            .newsitem:nth-child(even){
                flex-direction: column;
            }
            .newsimg{
                height:250px;
                width:100%;
            }
            .newscontent{
                width:100%;
                height:300px;
            }
            .newscontent .buttonarea{
                margin-top:9px;
                margin-bottom: 5px;
            }
        }
</style>