<template>
    <div class="content">
        <div id="change">
            <div id='form' v-if='!msg'>
                <!-- <h1>密碼修改</h1> -->
                <div id="changeContent">
                    <div class='formitem'>
                        <label for="oldPassword" class='text'>原密碼</label>
                        <input type="password" class="inputtext" id='oldPassword' name='oldPassword'>
                    </div>
                    <div class='formitem'>
                        <label for="password" class='text'>新密碼</label>
                        <input type="password" class="inputtext" id='password' name='password'>
                    </div>
                    <div class='formitem'>
                        <label for="password2" class='text'>新密碼確認</label>
                        <input type="password" class="inputtext" id='password2' name='password2'>
                    </div>
                    <div class='formitem'>
                        <input type="submit" value="確認修改" @click='submitForm($event)'></input>
                    </div>
                </div>
            </div>
            <div class="result" v-if='msg'>
                <p v-text='msg'></p>
            </div>
        </div>
    </div>
</template>

<script>

export default ({
    data(){
        return{
            msg:null
        }
    },
    methods:{
        submitForm(e){
            e.preventDefault();
            const passwordRules = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d]{5,}$/;
            if (!passwordRules.test(document.querySelector('#oldPassword').value)) {
                document.querySelector('#oldPassword').focus();
                alert('舊密碼格式有誤，請輸入正確的格式');
                return;
            }
            if (!passwordRules.test(document.querySelector('#password').value)) {
                document.querySelector('#password').focus();
                alert('新密碼格式有誤，請輸入正確的格式');
                return;
            } else if (document.querySelector('#password').value !== document.querySelector('#password2').value) {
                document.querySelector('#password2').focus();
                alert('新密碼與確認密碼不一致，請再次確認');
                return;
            }
            axios.post('/api/data.php',JSON.stringify({
                username:Cookies.get('username'),
                oldpassword:document.querySelector('#oldPassword').value,
                password:document.querySelector('#password2').value,
                dataKey:6
            }))
            .then(res=>{
                this.msg = res.data;
                setTimeout(()=>{
                    this.$emit('status-Update',true);
                },1500);
                
            })

        }
    }
})
</script>

<style scoped>
        #content {
            /* height: 100vh; */
            height: 800px;
            width: 100%;
            background-color: #000;
        }

        #change {
            position: relative;
            /* top: -50px; */
            /* left: 18.5%; */
            height: 39.5%;
            /* width: 60.5%; */
            /* background-color: #000; */
        }
        #form{
            min-height: 380px;
        }

        #changeContent {
            position: relative;
            top: 4vh;
            height: 320px;
        }

        .text {
            float: left;
            text-align: center;
            width: 20%;
            height: 50px;
            color: white;
            font-size: 28px;
            /* margin-top: -2px; */
            line-height:50px;
        
        }

        .inputtext {
            outline: none;
            height: 35px;
            width: 75%;
            border-radius: 17px;
            margin-bottom: 50px;
            font-size: 25px;
            background: #000;
            color: #fff;
        }

        .formitem {
            text-align: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }

        .formitem button,
        .formitem input {
            background: none;
            color: white;
            font-size: 1.2rem;
            font-weight: bold;
            border-radius: 29px;
        }

        .formitem input[type='submit'] {
            padding: 10px 15px;
            cursor: pointer;
            color: white;
            box-sizing: border-box;
            border-radius: 29px;
            background: #000;
            border: 2px solid rgb(235, 235, 235);
        }

        .formitem input[type='submit']:hover {
            background: rgb(235, 235, 235);
            color: black;
        }

        .formitem button:nth-child(2) {
            margin-left: 50px;
        }
        .result{
            position: relative;
            top:75px;
        }
        .result p {
            color: white;
            font-size: 24px;
        }

        .result a {
            color: white;
            text-decoration: none;
            border: 1px solid white;
            border-radius: 5px;
            padding: 5px 10px;
        }

        .result a:hover {
            color: black;
            background: #F5F5F5;
        }


        @media screen and (max-width:1200px) {
            .text {
                width: 100px;
                font-size: 18px;
                margin-top: 4px;
                line-height:41px
            }
        }

        @media screen and (max-width:731px) {
            .text {
                width: 100%;
                text-align: center;
                height: 35px;
                line-height:35px;
                
            }
            form{
                min-height: 530px;
            }
            .inputtext {
                width: 100%;
            }

        }

        @media screen and (max-width:576px) {
            #span {
                height: 40px;
                background-color: #000;
            }

            form{
                min-height: 440px;
            }
            .formitem button,
            .formitem input {
                margin: 10px 0;
            }

            .formitem button:nth-child(2) {
                margin-left: 0;
            }
        }
</style>
