import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import News from '../views/News.vue'
import ProductList from '../views/Product.vue'
import ProductContent from '../views/ProductContent.vue'
import ShoppingCart from '../views/ShoppingCart.vue'
import UserData from '../views/UserData.vue'
import Login from '../views/Login.vue'
import Registered from '../views/Registered.vue'
import NewsContent from '../views/NewsContent.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    beforeEnter(to,from,next){
      document.title = '龍臣車業';
      next();
    }
  },
  {
    path:'/news',
    name:'news',
    component:News,
    beforeEnter(to,from,next){
      document.title = '最新消息';
      next();
    }
  },
  {
    path:'/product',
    name:'product',
    component:ProductList,
    beforeEnter(to,from,next){
      document.title = '商品列表';
      next();
    }
  },
  {
    path:'/productContent/:id',
    name:'productContent',
    component:ProductContent,
    meta: { productContent: true }
  },
  {
    path:'/shoppingCart',
    name:'shoppingCart',
    component:ShoppingCart,
    meta:{shoppingCart:true},
    beforeEnter(to,from,next){
      document.title = '購物車';
      next();
    }
  },
  {
    path:'/userData',
    name:'userData',
    component:UserData,
    meta:{userData:true},
    beforeEnter:(to,form,next)=>{
      document.title = '會員專區';
      next();
    },
  },
  {
    path:'/login',
    name:'login',
    component:Login,
    meta:{login:true},
    beforeEnter:(to,form,next)=>{
      document.title = '會員登入';
      next();
    },
  },
  {
    path:'/registered',
    name:'registered',
    component:Registered,
    meta:{registered:true},
    beforeEnter:(to,form,next)=>{
      document.title = '會員註冊';
      next();
    },
  },
  {
    name:'newsContent',
    path:'/newsContent/:id',
    component:NewsContent,
  },
  {
    path:'*',
    redirect:'/',
    beforeEnter:(to,form,next)=>{
      document.title = '龍臣車業';
      next();
    },
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  window.scroll(0,0);
  next();
})
export default router
