<template>
    <main>
        <Logining  v-if='!newsData' class='lodining'/>
        <h1 v-if='newsData' v-text='newsData[0].title'> </h1>
        <div class="content" v-if='newsData'>
           <p class='newsContent' v-html='newsData[0].content'> </p> 
           <p class='date' v-text='newsData[0].date'> </p>
        </div>
    </main>
</template>

<script>
import Logining from '@/components/Logining.vue'
export default ({
   data(){
       return{
           newsData:null
       }
   },
   components:{
       Logining,
   },
   beforeRouteEnter(to,from,next){
       next(vm=>{
           vm.newsData = null;
           axios.post('/api/data.php',JSON.stringify( {id:to.params.id,dataKey:9}))
            .then(res=>{
                vm.newsData = res.data;
            })
       })
    
   },
   beforeRouteUpdate(to,from){
       this.newsData = null;
    
       axios.post('/api/data.php',JSON.stringify( {id:to.params.id,dataKey:9}))
            .then(res=>{
                this.newsData = res.data;
            })
   }
})
</script>


<style scoped>
        main{
            display: grid;
            grid-template-rows: 140px 60px 1fr;
            grid-template-columns: 20vw 60vw 20vw;
            background-color: #000;
            padding-bottom: 40px;
        }
        .lodining{
            grid-row: 2/3;
            grid-column: 2/3;
        }
        main h1{
            grid-row: 2/3;
            grid-column: 2/3;
            font-size: 1.9rem;
            font-weight: bold;
            color:white;
            padding-left: 5px;
            position: relative;
            text-align: left;
        }
        main h1::after{
            content: '';
            position: absolute;
            border-bottom: 3px red solid;
            width:100%;
            left:0px;
            bottom: 20px;
        }
        main .content{
            position: relative;
            border: 3px solid rgb(235, 235, 235);
            border-radius: 10px ;
            grid-row: 3/4;
            grid-column: 2/3;
            padding:2vh 5vw 4vh 5vw;
            color:white;
            min-height: 300px;
            font-size: 1.6rem;
            margin-bottom: 40px;
            text-align: left;
        }
        main .content button{
            position: absolute;
            border: 1px solid rgb(177, 177, 177);
            border-radius: 10px;
            background-color: #000;
            color:azure;
            bottom:-50px;
            right:20px;
            padding: 5px 20px;
            font-weight: bold;
            cursor: pointer;
            transition: all 0.4s;
        }
        main .content button:hover{
            background-color: rgb(189, 189, 189);
            color:black
        }
        main .content button:active{
            background-color: rgb(90, 90, 90);
            color:rgb(226, 226, 226)       
        }

        main .content .date{
            position: absolute;
            bottom:20px;
            right:2vw;
            color:white; 
            margin-top:40px;
        }
        main .content .newsContent{
            margin-bottom:50px;
        }
</style>