<template>
    <div class='addressitem'>
        <div>
            <select name="city" id="city" v-model='city' @input='set(1)'>
                <option value='null' disabled>請選擇</option>
                <option  v-for='item,index of post' :value="index">{{item.CityName}}</option>
            </select>
        </div>
        <div>
            <select name="area" id="area" v-model='area' @input='set(2)'>
                <option value='null' disabled>請選擇</option>
                <option  v-for='item,index of areaData' :value="index" >{{item.AreaName}}</option>
            </select>
        </div>
        <div>
            <select name="road" id="road" v-model='road' @input='set(3)'>
                <option value='null' disabled>請選擇</option>
                <option  v-for='item,index of roadData' :value="index">{{item.RoadName}}</option>
            </select>
        </div>
    </div>
</template>

<script>
const addressData = require('@/assets/AllData.json');

export default ({
    data(){
       return{
           city:null,
           area:null,
           road:null
       }
    },
    computed:{
        post(){
            return addressData;
        },
        areaData(){
            if(this.city || this.city===0){
                return this.post[this.city].AreaList;
            }
            else{
                return null;
            }
        },
        roadData(){
            if(this.area || this.area===0){
                return this.areaData[this.area].RoadList;
            }
            else{
                return null;
            }
        },
        allname(){
            if(this.city || this.city===0){
                if(this.area || this.area===0){
                    if(this.road  || this.road===0){
                        return this.post[this.city].CityName  + this.areaData[this.area].AreaName + this.roadData[this.road].RoadName;
                    }
                    else{   
                        return this.post[this.city].CityName  + this.areaData[this.area].AreaName;
                    }
                }
                else{
                    return this.post[this.city].CityName;
                }
            }
            else{
                return '';
            }
        }
    },
    methods:{
        set(key){
            if(key == 1){
                this.area = null;
                this.road = null;
            }
            if(key ==2){
                this.road = null;
            }
            this.$nextTick(function(){
                this.$emit('Address',this.allname);
            })
            
        },
    }
})
</script>



<style scoped>
  select{
    background: left top rgba(68, 68, 68, 1);
    color: rgba(255, 255, 255, 1);
    font-size: 14px;
    border: 1px solid rgba(0, 0, 0, 1);
    padding: 5px
  }
  .addressitem{
      width:100%;
      max-width:40vw;
      text-align: left;
  }
  .addressitem div{
      display: inline-block;
  }

  @media screen and (max-width:1300px) {
      .addressitem{
          text-align: center;
          max-width:inherit;
      }
      .addressitem div{
          margin-top: 5px;
      }
  }
</style>