<template>
    <main>
        <div id='form'>
                <transition-group name='Data'>
                    <div v-if="userdata && status" key='block-a'>
                        <div class='formItem'>
                            <label for="name">姓名</label>
                            <input type="text" name="name" id='name' required placeholder='您的姓名' v-model='userdata[0].name' >
                        </div>
                        <div class='formItem'>
                            <label for="idNumber">身分證字號</label>
                            <input type="text" name="idNumber" id='idNumber' required placeholder='您的身分證字號' v-model='userdata[0].idNumber' ref='idNumber'>
                        </div>
                        <div class='formItem'>
                            <label for="phone">手機號碼</label>
                            <input type="text" name="phone" id='phone' required placeholder='您的手機號碼' v-model='userdata[0].phone' ref='phone'>
                        </div>
                        <div class='formItem' >
                            <label for="email">電子信箱</label>
                            <input type="email" name="email" id='email' required placeholder='您的信箱' v-model='userdata[0].email' ref='email'>
                        </div>
                        <div class='formItem'>
                            <label for="address">地址</label>
                            <textarea rows="5" name="address" id='address' required v-model='userdata[0].address'>  </textarea>
                        </div>
                        <div class='formItem'>
                            <input type="submit" value='確認修改' @click.prevent ='checkFrom()'>
                            <input type="hidden" ref='username' name='username'>
                        </div>
                    </div>
                    <div v-if='!status' key='block-b'>
                        <Logining></Logining>
                    </div>
                    <div v-if="!userdata && status" style="color: #fff; text-align: center; font-size: 1.6rem; margin-bottom: -2vh;" key='block-c'>
                        未找到對應的資料，請重新登入後再作嘗試。
                    </div>
                    <div v-if="submit" style="color: #fff; text-align: center; font-size: 1.6rem; margin-bottom: -2vh;" key='block-d' v-text='msg'>
                        
                    </div>
                </transition-group>
        </div>
    </main>
</template>

<script>
import Logining from '@/components/Logining.vue'

export default ({
            data(){
                return {
                    status:false,
                    cookiesData:null,
                    submit:false,
                    msg:null,
                };
            },
            components:{
                Logining,
            },
            props:['userdata'],
            created(){
                if(userdata){
                    this.status = true;
                }
                else{
                    this.$nextTick(function(){
                        this.status = true;
                    });
                }
            },
            methods:{
                checkFrom(){
                    const emailRules= /^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
                    const phoneRules = /^09\d{8}$/;
                    const idRules = /^[A-Z]{1}[1-2]{1}[0-9]{8}$/;
                    if(!idRules.test(this.userdata[0].idNumber)){
                        this.$refs.idNumber.focus();
                        alert('身分證有誤，請再次確認');
                        return;
                    }
                    if(!phoneRules.test(this.userdata[0].phone)){
                        this.$refs.phone.focus();
                        alert('手機號碼有誤，請再次確認');
                        return;
                    }
                    if(!emailRules.test(this.userdata[0].email)){
                        this.$refs.email.focus();
                        alert('信箱格式有誤，請確認');
                        return;
                    }            
                    this.$refs.username.value = this.cookiesData;
                    axios.post('/api/data.php', JSON.stringify({userdata:this.userdata[0],dataKey:5}))
                    .then(res=>{
                        this.submit = true;
                        this.msg = res.data;
                        setTimeout(()=>{
                            this.$emit('status-Update',true);
                        }
                        ,1500);
                    })


                }
            }
})
</script>

<style scoped>
        main{
            width:100%;
            padding-bottom: 40px;
            /* margin-left: -10vw; */
        }
        main #form {
            position: relative;
            padding: 5vh 5vw;
        }
        main #form .formItem {
            display: flex;
            flex-wrap: wrap;
            /* margin-left:20vw; */
        }
        main #form .formItem + .formItem{
            margin-top: 5vh;
        }
        main #form .formItem label{
            font-size: 18px;
            line-height: 30px;
            height: 30px;
            color:white;
            font-weight: bold;
            margin-right: 2vw;
            width:90px;
            text-align: center;
        }
        main #form .formItem input{
            border: 2px solid rgb(235, 235, 235) ;
            border-radius: 30px;
            background-color: black;
            height: 20px;
            color: white;
            width: 100%;
            max-width: 40vw;
            font-size: 15px;
        }
        main #form .formItem textarea{
            background-color: black;
            border: 2px solid rgb(235, 235, 235) ;
            border-radius: 5px;
            color: white;
            width: 100%;
            max-width: 40vw;
            font-size: 18px;
            resize: none; 
        }
        main #form .formItem input:focus{
            outline: none;
        }
        main #form .formItem input[type='submit']{
            margin-left: auto;
            margin-right: 3vw;
            height: 35px;
            width:100px;
            font-weight: bold;
            cursor: pointer;
        }
        main #form .formItem input[type='submit']:hover{
            background-color: rgb(189, 189, 189);
            color:black
        }
        main #form .formItem input[type='submit']:active{
            background-color: rgb(90, 90, 90);
            color:rgb(226, 226, 226)
        }
        footer{
            text-align: center;
            background: #000;
            color: white;
        }
        .Data-enter-active{
            position: relative;
            top:60px;
            opacity: 0;
            transition: all 0.8s;
        }
        .Data-enter-to{
            top:0px;
            opacity: 1;
        }
        @media screen and (max-width:1300px){
            main #form .formItem label{
                text-align: inherit;
            }
            main #form .formItem input{
                max-width: inherit;
            }
            main #form .formItem textarea{
                max-width: inherit;
            }
            main #form .formItem input[type='submit']{
                margin-right: inherit;
            }
        }
</style>