<template>
    <div id='main'>
        <div class="product" id='product'>
            <h2>產品介紹</h2>
            <div class="productlist">
                <h3>大型重型機車</h3>
                <div class='productitem' v-for='item of prducttype1' :key='item.id'>
                    <router-link :to="`productContent/`+ item.id">
                        <img :src="'image/'+item.id+'/'+item.color1" alt="" class='productimg'>
                        <h4 class='productname'>{{item.typename}}</h4>
                    </router-link>
                </div>
            </div>

            <div class="productlist">
                <h3>150cc~250cc以下</h3>
                <div class='productitem' v-for='item of prducttype2' :key='item.id'>
                    <router-link :to="`productContent/`+ item.id">
                        <img :src="'image/'+item.id+'/'+item.color1" alt="" class='productimg'>
                        <h4 class='productname'>{{item.typename}}</h4>
                    </router-link>
                </div>
            </div>

            <div class="productlist">
                <h3>125cc~150cc以下</h3>
                <div class='productitem' v-for='item of prducttype3' :key='item.id'>
                    <router-link :to="`productContent/`+ item.id">
                        <img :src="'image/'+item.id+'/'+item.color1" alt="" class='productimg'>
                        <h4 class='productname'>{{item.typename}}</h4>
                    </router-link>
                </div>
            </div>

            <div class="productlist">
                <h3>100cc~125cc以下</h3>
                <div class='productitem' v-for="item of prducttype4" :key='item.id'>
                    <router-link :to="`productContent/`+ item.id">
                        <img :src="'image/'+item.id+'/'+item.color1" alt="" class='productimg'>
                        <h4 class='productname'>{{item.typename}}</h4>
                    </router-link>
                </div>

            </div>

            <div class="productlist">
                <h3>電動車</h3>
                <div class='productitem' v-for='item of prducttype5' :key='item.id'>
                    <router-link :to="`productContent/`+ item.id">
                        <img :src="'image/'+item.id+'/'+item.color1" alt="" class='productimg'>
                        <h4 class='productname'>{{item.typename}}</h4>
                    </router-link>
                </div>

            </div>


        </div>
    </div>
</template>

<script>
export default ({
            data() {
                return {
                    resdata: null,
                }
            },
            computed: {
                prducttype1() {
                    if (this.resdata) {
                        return this.resdata.filter(e => {
                            return e.class === '大型重型機車';
                        });
                    }
                },
                prducttype2() {
                    if (this.resdata) {
                        return this.resdata.filter(e => {
                            return e.class === '150cc_250cc';
                        });
                    }
                },
                prducttype3() {
                    if (this.resdata) {
                        return this.resdata.filter(e => {
                            return e.class === '125cc_150cc';
                        });
                    }
                },
                prducttype4() {
                    if (this.resdata) {
                        return this.resdata.filter(e => {
                            return e.class === '100cc_125cc';
                        });
                    }
                },
                prducttype5() {
                    if (this.resdata) {
                        return this.resdata.filter(e => {
                            return e.class === '電動車';
                        });
                    }
                },
            },
            created() {
                axios.post('/api/data.php',JSON.stringify({dataKey:10}))
                    .then(res => {
                        this.resdata = res.data;
                    });
            },
})
</script>

