<template>
    <main>
        <div class="form" id='form'>
            <div class='formItem'>
                <label for="username">帳號</label>
                <input type="text" name="username" id='username' required placeholder='您的帳號'  v-model='username'>
            </div>
            <div class='formItem'>
                <label for="idNumber">身分證字號</label>
                <input type="text" name="idNumber" id='idNumber' required placeholder='您的身分證字號' v-model='idNumber' >
            </div>
            <div class='formItem' >
                <label for="email">電子信箱</label>
                <input type="email" name="email" id='email' required placeholder='您的信箱' v-model='email'>
            </div>
            <div class='formItem'>
                <button type="button" id='submit' @click="submitForm">送出查詢</button>
            </div>
            <div class="formItem resData" v-if='status'>
                <p v-text='msg'></p>
                <button type="button" id='submit' @click="prev">回登入頁面</button>
            </div>
        </div>
    </main>
</template>

<script>

export default ({
    data(){
        return{
            username:null,
            idNumber:null,
            email:null,
            status:false,
            resData:null,
            msg:null,
            dataKey:13
        }
    },
    methods:{
        submitForm(){
            const emailRules= /^\w+([-+.']\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
            const idRules = /^[A-Z]{1}[1-2]{1}[0-9]{8}$/;
            const usernameRules = /^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d]{5,}$/;
            if(!usernameRules.test(this.username)){
                document.querySelector('#username').focus();
                alert('帳號格式有誤，請輸入正確的格式');
                return;
            }
            if(!idRules.test(this.idNumber)){
                document.querySelector('#idNumber').focus();
                alert('身分證格式有誤，請再次確認');
                return;
            }
            if(!emailRules.test(this.email)){
                document.querySelector('#email').focus();
                alert('信箱格式有誤，請確認');
                return;
            }
            axios.post('/api/data.php',JSON.stringify(this.$data))
                .then(res=>{
                    this.resData = res.data;
                    if(!this.status){
                        if(this.resData){
                            this.msg = `您的密碼是：${this.resData}`;
                        }
                        else{
                            this.msg = '查無資料，請確認您所輸入的資料是否正確。';
                        }
                        this.status = true;
                    }
                    else{
                        if(this.resData){
                            this.msg = `您的密碼是：${this.resData}`;
                        }
                        else{
                            this.msg = '查無資料，請確認您所輸入的資料是否正確。';
                        }
                    }
                })            
        },
        prev(){
            this.$emit('status-Update',true);
        },
    }
})
</script>

<style scoped>
       main{
            margin-top:15px;
            background-color: #000;
            padding-bottom: 40px;
        }
        main .form{
            border: 3px solid rgb(235, 235, 235);
            border-radius: 10px ;
            padding:2vh 5vw 4vh 5vw;
        }
        main .form .formItem {
            display: flex;
            flex-wrap: wrap;
        }
        main .form .formItem + .formItem{
            margin-top: 5vh;
        }
        main .form .formItem label{
            font-size: 18px;
            line-height: 30px;
            height: 30px;
            color:white;
            font-weight: bold;
            margin-right: 2vw;
            width:90px;
            text-align: center;
        }
        main .form .formItem input{
            border: 2px solid rgb(235, 235, 235) ;
            border-radius: 30px;
            background-color: black;
            height: 20px;
            color: white;
            width: 100%;
            max-width: 40vw;
            font-size: 15px;
        }
        main .form .formItem input:focus{
            outline: none;
        }
        main .form .formItem button{
            border: 2px solid rgb(235, 235, 235) ;
            border-radius: 30px;
            background-color: black;
            color:white;
            margin-left: auto;
            margin-right: auto;
            height: 35px;
            width:100px;
            font-weight: bold;
            cursor: pointer;
        }
        main .form .formItem p{
            width:100%;
            margin-bottom:15px;
        }
        main .form .formItem button:hover{
            background-color: rgb(189, 189, 189);
            color:black
        }
        main .form .formItem button:active{
            background-color: rgb(90, 90, 90);
            color:rgb(226, 226, 226)
        }
        main .form .formItem.resData{
            color: white;
            display: flex;
            justify-content: center;
        }
        @media screen and (max-width:1299px) {
            main .form .formItem label{
                text-align: left;
            }
        }
</style>